.main__avatar {
  position: relative;
  padding-bottom: 100%;

  @media (max-width: 767px) {
    padding-bottom: 270px;
    width: 270px;
    margin-top: 50px;
  }


  img {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    z-index: 5;
    object-fit: cover;
  }

  span {
    position: absolute;
    top: 0px;
    bottom: 0;
    z-index: -9;
    height: 100%;
    width: 92%;
    border: 5px solid #4e93e6;
    transform: scale(1.13) translate(10%, 0px);
    left: 0;
    right: 0;
    z-index: 4;
  }
}